import "./App.css";
import React, { useEffect, useState, useRef } from "react";
import About from "./Components/About";
import Skills from "./Components/Skills";
import Projects from "./Components/Projects";
import Experience from "./Components/Experience";
import Contact from "./Components/Contact";

function App() {
  const [activeSection, setActiveSection] = useState("");
  const [progress, setProgress] = useState(0);
  const [markerPositions, setMarkerPositions] = useState([]);
  const progressContainerRef = useRef(null);

  const sections = [
    { id: "welcome", title: "Welcome" },
    { id: "about", title: "About" },
    { id: "skills", title: "Skills" },
    { id: "projects", title: "Projects" },
    { id: "experience", title: "Experience" },
    { id: "contact", title: "Contact" },
  ];

  useEffect(() => {
    const calculateProgress = () => {
      const windowHeight = window.innerHeight;
      const scrollTop = window.scrollY;
      const docHeight = document.documentElement.scrollHeight - windowHeight;

      const scrollProgress = (scrollTop / docHeight) * 100;
      setProgress(Math.min(100, Math.max(0, scrollProgress)));

      const sectionElements = sections.map((section) => ({
        id: section.id,
        element: document.getElementById(section.id),
      }));

      const viewportMiddle = scrollTop + windowHeight / 2;
      for (const section of sectionElements) {
        if (section.element) {
          const rect = section.element.getBoundingClientRect();
          const absoluteTop = scrollTop + rect.top;
          const absoluteBottom = absoluteTop + rect.height;

          if (
            viewportMiddle >= absoluteTop &&
            viewportMiddle <= absoluteBottom
          ) {
            setActiveSection(section.id);
            break;
          }
        }
      }

      if (progressContainerRef.current) {
        const positions = sectionElements.map((section) => {
          if (section.element) {
            const rect = section.element.getBoundingClientRect();
            const absoluteTop = scrollTop + rect.top;
            const percentage = (absoluteTop / docHeight) * 100;
            return Math.min(100, Math.max(0, percentage));
          }
          return 0;
        });
        setMarkerPositions(positions);
      }
    };

    window.addEventListener("scroll", calculateProgress);
    window.addEventListener("resize", calculateProgress);
    calculateProgress();

    return () => {
      window.removeEventListener("scroll", calculateProgress);
      window.removeEventListener("resize", calculateProgress);
    };
  }, [sections]);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      const elementRect = element.getBoundingClientRect();
      const absoluteElementTop = elementRect.top + window.scrollY;
      const windowHeight = window.innerHeight;
      const middle =
        absoluteElementTop - windowHeight / 2 + elementRect.height / 2;

      window.scrollTo({
        top: middle,
        behavior: "smooth",
      });
    }
  };

  useEffect(() => {
    const observerCallback = (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add("visible");
        }
      });
    };

    const observer = new IntersectionObserver(observerCallback, {
      threshold: 0.1,
    });

    const sections = document.querySelectorAll(".slide-in");
    sections.forEach((section) => observer.observe(section));

    return () => sections.forEach((section) => observer.unobserve(section));
  }, []);

  return (
    <div>
      <main>
        <div className="progress-container">
          <div
            className="progress-line"
            style={{
              "--progress-height": `${progress}%`,
            }}
          ></div>
          <div className="section-markers">
            {sections.map((section, index) => {
              const sectionProgress = (index / (sections.length - 1)) * 100;
              const isActive = sectionProgress <= progress;
              const isCurrent = section.id === activeSection;

              return (
                <div
                  key={section.id}
                  className={`section-marker ${isActive ? "active" : ""} ${
                    isCurrent ? "current" : ""
                  }`}
                  style={{
                    top: `${sectionProgress}%`,
                    transform: "translateY(-50%)",
                  }}
                  onClick={() => scrollToSection(section.id)}
                >
                  <span className="section-title">{section.title}</span>
                </div>
              );
            })}
          </div>
        </div>

        <div id="welcome" className="welcome-section">
          <h1>
            Welcome, I'm{" "}
            <span className="name">
              <span>M</span>
              <span>a</span>
              <span>u</span>
              <span>r</span>
              <span>a</span>
              <span>&nbsp;</span>
              <span>S</span>
              <span>w</span>
              <span>e</span>
              <span>e</span>
              <span>n</span>
              <span>e</span>
              <span>y</span>
            </span>
          </h1>
        </div>
        <About />
        <Skills />
        <Projects />
        <Experience />
        <Contact />
      </main>
    </div>
  );
}

export default App;
